import { Component } from '@angular/core';
import { IonicPage, NavController,ModalController,PopoverController } from 'ionic-angular';

/**
 * The Welcome Page is a splash page that quickly describes the app,
 * and then directs the user to create an account or log in.
 * If you'd like to immediately put the user onto a login/signup page,
 * we recommend not using the Welcome page.
*/
@IonicPage()
@Component({
  selector: 'page-welcome',
  templateUrl: 'welcome.html'
})
export class WelcomePage {

  constructor(public navCtrl: NavController, public modalCtrl: ModalController, public popOverCtrl: PopoverController ) { }

  login() {
    this.navCtrl.push('LoginPage');
  }

  signup() {
  	console.log('a');
    this.navCtrl.push('SignupPage');
  }

  popover(){
  	let popover = this.popOverCtrl.create('SettingsPage');
  	popover.present()
  }

 modal(){
 	const modal = this.modalCtrl.create('SettingsPage');
 	modal.onDidDismiss(data => {
     console.log(data);
   });
    modal.present();
 }
}



